nav {
	border-radius: 5px;
	width: 20%;
	background: #252525;
	margin: 15px 15px 0 0;
	.best-friends {
		display: flex;
		justify-content: center;
		flex-wrap: wrap; } }
ul {
	margin: 50px 15px 15px 15px; }
li {
	margin-bottom: 20px;
	text-align: center;
	padding: 5px;
	transition: 0.5s;
	font-size: 18px;
	&:hover {
		background: #FF7400;
		cursor: pointer; } }
